.glitch-effect {
  position: relative;
  display: inline-block;
}
.glitch-effect::before,
.glitch-effect::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.8;
}
.glitch-effect::before {
  animation: glitch-1 2s infinite;
  color: #ff0040;
  z-index: -1;
}
.glitch-effect::after {
  animation: glitch-2 2s infinite;
  color: #40e0d0;
  z-index: -2;
}
.neon-glow {
